import React from "react";
import "./home.css";
import homePhoto from "../asse/home-photo.webp";
import small1 from "../asse/home1.webp";
import small2 from "../asse/home2.webp";
import { Link } from "react-router";

export default function Home() {
  return (
    <>
      <div className="home-container">
        <img src={homePhoto} alt="Sommartider" className="home-image" />
        <div className="overlay-box">
        <h2>Sommartider.</h2>
        <p>
          Vlkommen till ett hav av sommarlek! Utforska vrt frgglada sortiment av roliga och spnnande sommarleksaker  perfekta fr skna dagar i trdgrden, plask och stoj vid stranden eller fartfyllda ventyr i parken. Oavsett om du vill bygga sandslott, spela vattenkrig eller bara njuta av solskenet med vnner och familj, har vi leksakerna som frvandlar varje sommardag till ett minne fyllt av skratt och gldje.
        </p>
      </div>
      </div>

      <hr />

      <section className="small-images-container">
        <div className="small-image-box">
          <img src={small1} alt="Leksak 1" className="small-image large" />
          <Link to="/produkter" className="gul-knapp">Till alla produkter</Link>
        </div>
        
        <div className="small-image-box">
        <p className="text2">Upptck sommarens roligaste nyheter!</p>
          <img src={small2} alt="Leksak 2" className="small-image small" />
          <p className="small-image-text"></p>
        </div>
    </section>
    </>
  );
}
