import Joi from "joi";

export const productSchema = Joi.object({
  namn: Joi.string()
    .min(3)
    .pattern(/^[a-zA-Z\s]+$/)
    .required()
    .messages({
      "string.empty": "Produktnamn fr inte vara tomt.",
      "string.min": "Namn mste vara minst 3 bokstver.",
      "string.pattern.base": "Namn fr bara innehlla bokstver och mellanslag.",
      "any.required": "Produktnamn r obligatoriskt."
    }),

    pris: Joi.string()
      .pattern(/^\d+$/)
      .custom((value, helpers) => {
        const number = parseInt(value, 10);
        if (number < 1) {
          return helpers.message("Pris mste vara minst 1 kr.");
        }
        return value;
      })
      .required()
      .messages({
        "string.empty": "Pris fr inte vara tomt.",
        "string.pattern.base": "Pris mste vara ett heltal (utan 'kr').",
        "any.required": "Pris r obligatoriskt."
      }),


  bild: Joi.string()
    .uri()
    .pattern(/^https:\/\//)
    .required()
    .messages({
      "string.empty": "Bild-URL fr inte vara tom.",
      "string.uri": "Bild-URL mste vara en giltig lnk.",
      "string.pattern.base": "Bild-URL mste brja med 'https://'.",
      "any.required": "Bild-URL r obligatorisk."
    })
});
